/* =========================
   Base / Reset
   ========================= */
body {
  margin: 0;
  font-family: "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
  background: linear-gradient(135deg, #a8edea 0%, #fed6e3 100%);
  color: #222;
  min-height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 10px;
  box-sizing: border-box;
  direction: rtl;
}

/* =========================
   Primary Layout Containers
   (base definitions)
   ========================= */
.player-setup,
.finish-screen,
.game-container {
  max-width: 900px; /* הגדלתי מ-800 ל-900 */
  width: 100%;
  margin: 40px auto;
  padding: 20px 32px; /* הרחבתי את הפדינג משני הצדדים */
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
}

/* =========================
   Forms (Player Setup)
   ========================= */
.player-setup form label {
  display: block;
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.player-setup input[type="text"] {
  width: 100%;
  padding: 8px;
  font-size: 1rem;
  margin-top: 5px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.player-setup div {
  margin-bottom: 15px;
  font-size: 1.2rem;
}

.player-setup input[type="radio"] {
  margin-left: 10px;
}

/* =========================
   Text / Messages
   ========================= */
.message {
  font-size: 1.1rem;
  margin-top: 10px;
  min-height: 1.5rem;
}

.message.correct {
  color: #4caf50;
  font-weight: 700;
}

.message.wrong {
  color: #f44336;
  font-weight: 700;
}

.loading-message {
  font-size: 1.3rem;
  font-weight: 600;
  padding: 50px;
}

/* =========================
   Shared Buttons
   ========================= */
.start-btn,
.restart-btn,
.back-btn,
.finish-btn,
.option-btn {
  cursor: pointer;
  border: none;
  outline: none;
  border-radius: 10px;
  padding: 12px 20px;
  font-size: 1.2rem;
  transition: all 0.3s ease;
  user-select: none;
}

.start-btn,
.restart-btn {
  background: #4caf50;
  color: white;
  font-weight: bold;
  width: 100%;
}

.start-btn:hover,
.restart-btn:hover {
  background: #43a047;
}

.back-btn {
  background: #f44336;
  color: white;
  margin-left: 10px;
  width: 100px;
}

.back-btn:hover {
  background: #d32f2f;
}

/* Dedicated finish button styling (kept later than shared) */
.finish-btn {
  background-color: #80c7ff;
  border: none;
  border-radius: 6px;
  padding: 8px 16px;
  font-size: 1rem;
  cursor: pointer;
  color: #fff;
  font-weight: bold;
  box-shadow: 0 2px 5px rgba(0,0,0,0.15);
  transition: background-color 0.3s ease;
}

.finish-btn:hover {
  background-color: #5aa6e6;
}

/* =========================
   Scoreboard
   ========================= */
.scoreboard-container {
  max-width: 600px;
  margin: 0 auto 20px;
  padding: 0 16px;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.game-status-title {
  font-size: 1.3rem;
  font-weight: 600;
  color: #333;
  user-select: none;
  font-weight: bold;
}

.scoreboard-stats {
  display: flex;
  flex-direction: column;
  gap: 6px;
  padding: 0 10px;
  font-size: 1.1rem;
  font-weight: 500;
  color: #222;
  direction: rtl;
}

.scoreboard-stats.row {
  display: flex;
  justify-content: space-between;
  gap: 12px;
  padding: 0 10px;
  font-size: 1.05rem;
  font-weight: 500;
  color: #222;
  direction: rtl;
  margin-bottom: 6px;
}

.scoreboard-stats-row {
  display: flex;
  justify-content: space-between;
  font-size: 1.2rem;
  font-weight: 600;
  color: #222;
  padding: 6px 10px;
  border-bottom: 1px solid #ddd;
  margin-bottom: 8px;
}

.scoreboard-stats-row:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

/* כותרת לוח */
.scoreboard-title {
  margin: 0 0 6px 0;
  text-align: center;
  font-size: 1.1rem;
  color: #333;
}

/* בר עליון: שם + פעולות */
.top-actions {
  display: flex;
  align-items: center;
  gap: 8px;
}

.sound-icon {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 4px;
  border-radius: 50%;
  transition: background 0.2s ease;
}

.sound-icon:hover {
  background: rgba(0, 0, 0, 0.05);
}

/* =========================
   Question / Prompt
   ========================= */
.question-container {
  text-align: center;
}

.prompt {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  position: relative;
}

.prompt-text {
  user-select: none;
}

/* Speaker icon positioned on the left */
.prompt svg {
  position: absolute;
  left: 0;
  cursor: pointer;
  font-size: 1.5rem;
  transition: transform 0.3s ease;
}

.prompt svg:hover {
  transform: scale(1.2);
}

/* ניקוד עם אנימציה */
.score {
  font-size: 1.2rem;
  font-weight: bold;
  transition: transform 0.3s ease;
}

.score.bounce {
  transform: scale(1.3);
}

/* =========================
   Options (answers)
   ========================= */
.options {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-bottom: 15px;
}

.option-container {
  display: flex;
  align-items: center;
  gap: 10px;
  position: relative;
}

.option-container.has-speaker {
  padding-left: 40px;
}

/* Speaker icons in option containers */
.option-container svg {
  cursor: pointer;
  font-size: 1.2rem;
  transition: transform 0.3s ease;
  color: #555;
  flex-shrink: 0;
  position: absolute;
  left: 0;
}

.option-container svg:hover {
  transform: scale(1.2);
  color: #2196f3;
}

.option-btn {
  background: #eee;
  font-size: 1.3rem;
  padding: 12px;
  border-radius: 12px;
  box-shadow: 0 4px 7px rgba(0, 0, 0, 0.1);
  transition: background-color 0.3s ease;
  width: 100%;
  text-align: center;
  cursor: pointer;
  border: none;
}

.option-btn:hover:not(:disabled) {
  background-color: #d0f0fd;
}

.option-btn.correct {
  background-color: #4caf50 !important;
  color: white;
  pointer-events: none;
}

.option-btn.wrong {
  background-color: #f44336 !important;
  color: white;
  pointer-events: none;
}

/* טקסטים והודעות */
.message {
  font-size: 1.1rem;
  margin-top: 10px;
  min-height: 1.5rem;
}

.message.correct {
  color: #4caf50;
  font-weight: 700;
}

.message.wrong {
  color: #f44336;
  font-weight: 700;
}

/* טקסט בהמתנה לטעינה */
.loading-message {
  font-size: 1.3rem;
  font-weight: 600;
  padding: 50px;
}

/* =========================
   Finish Screen
   ========================= */
.finish-screen {
  text-align: center;
  padding: 20px;
  background: white;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  max-width: 500px;
  margin: 40px auto;
}

.trophy-icon {
  font-size: 3rem;
  margin-bottom: 10px;
}

/* כפתורי סיום במסך הסיום */
.finish-buttons {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 20px;
  flex-wrap: wrap;
}

.finish-buttons button {
  flex: 1;
  max-width: 180px;
  padding: 10px 20px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 8px;
  border: none;
  color: white;
  transition: background-color 0.3s ease;
  min-width: 120px;
}

.finish-buttons button:nth-child(1) {
  background-color: #2ecc71; /* התחלה מחדש */
}

.finish-buttons button:nth-child(1):hover {
  background-color: #27ae60;
}

.finish-buttons button:nth-child(2) {
  background-color: #e67e22; /* חזור למשחק */
}

.finish-buttons button:nth-child(2):hover {
  background-color: #d35400;
}

.finish-buttons .share-btn {
  background-color: #3498db; /* שתף */
}

.finish-buttons .share-btn:hover {
  background-color: #2980b9;
}

.share-btn {
  background-color: #3498db;
  color: white;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.share-btn:hover {
  background-color: #2980b9;
}

/* =========================
   Cards & Tables
   ========================= */
.card {
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.08);
  padding: 16px;
  box-sizing: border-box;
  margin: 16px auto;
  width: min(900px, 95vw); /* רחב ונוח גם במובייל */
}

/* כרטיס עליון: ברכה + טוגל */
.header-card {
  width: min(900px, 95vw);
  margin: 16px auto;
  padding: 14px 16px;
  background: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.08);
  box-sizing: border-box;
}

.header-card-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 12px;
}

.greeting {
  font-size: 1.2rem;
  font-weight: 700;
  color: #333;
}

/* Leaderboard */
.leaderboard-title {
  margin: 0 0 12px 0;
  text-align: center;
  font-size: 1.15rem;
  color: #333;
}

.leaderboard-table-wrap { overflow-x: auto; }

.leaderboard-table {
  width: 100%;
  border-collapse: collapse;
  table-layout: auto;
  background: #fff;
}

.leaderboard-table th,
.leaderboard-table td {
  border: 1px solid #eee;
  padding: 10px 8px;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  font-size: 0.95rem;
}

.leaderboard-table thead th {
  background: #f7f9fc;
  font-weight: 700;
  color: #333;
}

.leaderboard-table tbody tr:nth-child(even) { background: #fafafa; }

.leaderboard-empty {
  text-align: center;
  color: #666;
  padding: 10px 0;
  font-size: 0.95rem;
}

/* =========================
   Switch / Toggle (header sound)
   ========================= */
.switch {
  position: relative;
  display: inline-block;
  width: 46px;
  height: 24px;
}
.switch input { opacity: 0; width: 0; height: 0; }

.slider {
  position: absolute;
  cursor: pointer;
  inset: 0;
  background-color: #ccc;
  transition: 0.3s;
  border-radius: 34px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 18px;
  width: 18px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: 0.3s;
  border-radius: 50%;
}

input:checked + .slider { background-color: #4caf50; }
input:checked + .slider:before { transform: translateX(22px); }

.sound-label { font-size: 0.9rem; color: #333; }

/* =========================
   Adaptive Learning Section
   ========================= */
.adaptive-learning-card {
  background: white;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  text-align: center;
  max-width: 500px;
  margin: 0 auto;
}

.adaptive-header { margin-bottom: 25px; }

.adaptive-header h3 {
  margin: 0 0 10px 0;
  color: #333;
  font-size: 1.5rem;
}

.remaining-words { color: #666; font-size: 0.9rem; }

.word-display { margin-bottom: 25px; }

.source-word {
  font-size: 2.5rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 20px;
  padding: 15px;
  background: #f8f9fa;
  border-radius: 8px;
}

.speaker-section { margin-bottom: 20px; }

.speaker-button {
  background: #c6f9c7;
  color: white;
  border: none;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  font-size: 1.5rem;
  cursor: pointer;
  transition: all 0.3s ease;
  margin-bottom: 10px;
}

.speaker-button:hover:not(.disabled) {
  background: #43a047;
  transform: scale(1.1);
}

.speaker-button.disabled {
  background: #ccc;
  cursor: not-allowed;
  transform: none;
}

.instruction { color: #666; font-size: 0.9rem; margin-top: 10px; }

.translation {
  font-size: 2rem;
  font-weight: bold;
  margin: 20px 0;
  padding: 15px;
  border-radius: 8px;
  transition: all 0.3s ease;
}

.translation.color1 { background: #e3f2fd; color: #1976d2; border: 2px solid #1976d2; }
.translation.color2 { background: #f3e5f5; color: #7b1fa2; border: 2px solid #7b1fa2; }
.translation.color3 { background: #e8f5e8; color: #388e3c; border: 2px solid #388e3c; }

.click-counter { color: #666; font-size: 0.9rem; margin-top: 15px; }

.adaptive-actions { margin-top: 20px; }

.skip-button {
  background: #f44336;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.3s ease;
}

.skip-button:hover { background: #d32f2f; }

/* "לא יודע" כקישור */
.dont-know-wrap { display: flex; justify-content: center; margin-top: 6px; }
.dont-know-link {
  background: none;
  border: none;
  padding: 0;
  font-size: 1rem;
  text-decoration: underline;
  cursor: pointer;
  color: #1976d2;
}
.dont-know-link:hover:not(:disabled) { color: #0d47a1; text-decoration-thickness: 2px; }
.dont-know-link:disabled { opacity: 0.6; cursor: not-allowed; }

/* Mini table + continue */
.mini-table { width:100%; max-width:420px; margin:10px auto; border-collapse:collapse; font-size:0.95rem; }
.mini-table th, .mini-table td { border:1px solid #ddd; padding:6px 8px; text-align:center; }
.mini-table thead th { background:#f5f5f5; }
.continue-wrap { display:flex; justify-content:center; margin-top:10px; }

.next-btn {
  min-width: 200px;
  padding: 10px 18px;
  border: none;
  border-radius: 12px;
  background: linear-gradient(135deg, #4cafef, #1976d2);
  color: #fff;
  font-size: 1.05rem;
  font-weight: 600;
  letter-spacing: 0.5px;
  cursor: pointer;
  box-shadow: 0 4px 10px rgba(0,0,0,0.15);
  transition: all 0.25s ease;
}

.next-btn:hover:not(:disabled) {
  background: linear-gradient(135deg, #1976d2, #004ba0);
  transform: translateY(-2px);
  box-shadow: 0 6px 14px rgba(0,0,0,0.25);
}

.next-btn:active { transform: translateY(0); box-shadow: 0 3px 7px rgba(0,0,0,0.2); }
.next-btn:disabled { opacity: 0.6; cursor: not-allowed; }

/* =========================
   Responsive (base)
   ========================= */
@media (max-width: 768px) {
  .player-setup,
  .finish-screen,
  .game-container {
    max-width: 95%; /* 95% מרוחב המסך */
    margin: 20px auto;
    padding: 16px 20px; /* קצת פחות פדינג למובייל אבל עדיין רחב */
  }
}

/* =========================
   Overrides / Utilities (keep last)
   ========================= */
/* כופה רוחב מלא למיכל */
body, #root {
  width: 100vw !important;
  max-width: 100vw !important;
  overflow-x: hidden;
}

.game-container,
.finish-screen,
.player-setup {
  width: 100% !important;
  max-width: 900px !important;
  margin: 40px auto !important;
  padding-left: 40px !important;
  padding-right: 40px !important;
  box-sizing: border-box;
  background-color: #fff;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0,0,0,0.1);
  min-width: 300px; /* למקרה הצר ביותר */
}

/* במובייל, להשאיר רווח מהצדדים */
@media (max-width: 768px) {
  .game-container,
  .finish-screen,
  .player-setup {
    max-width: 95% !important;
    padding-left: 10px !important;
    padding-right: 10px !important;
  }
}
/* Container */
.scoreboard-container {
  --bg: #0f172a;
  --panel: #111827;
  --panel-2: #0b1220;
  --text: #e5e7eb;
  --muted: #94a3b8;
  --accent: #3b82f6;
  --accent-2: #22c55e;
  --danger: #ef4444;

  background: linear-gradient(180deg, var(--panel-2), var(--panel));
  color: var(--text);
  border-radius: 16px;
  padding: 16px;
  box-shadow: 0 8px 24px rgba(0,0,0,.25);
  border: 1px solid rgba(255,255,255,.06);
}

/* Top row */
.scoreboard-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 12px;
  margin-bottom: 12px;
}

.game-status-title {
  font-size: 18px;
  font-weight: 700;
  letter-spacing: .2px;
}

.scoreboard-top-right {
  display: flex;
  align-items: center;
  gap: 8px;
}

/* Combo badge */
.combo-badge {
  display: inline-flex;
  align-items: center;
  gap: 6px;
  background: rgba(251, 146, 60, .12);
  color: #fb923c;
  border: 1px solid rgba(251, 146, 60, .35);
  padding: 6px 10px;
  border-radius: 999px;
  font-size: 13px;
  animation: pulse 1.6s ease-in-out infinite;
}
.combo-badge strong { font-weight: 800; }
@keyframes pulse {
  0% { box-shadow: 0 0 0 0 rgba(251,146,60,.35); }
  70% { box-shadow: 0 0 0 12px rgba(251,146,60,0); }
  100% { box-shadow: 0 0 0 0 rgba(251,146,60,0); }
}

/* Finish button */
.finish-btn {
  background: linear-gradient(180deg, #ef4444, #b91c1c);
  color: #fff;
  border: none;
  padding: 8px 14px;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 700;
  transition: transform .1s ease, filter .2s ease;
}
.finish-btn:hover { filter: brightness(1.05); }
.finish-btn:active { transform: translateY(1px); }

/* Stats grid */
.score-cards {
  display: grid;
  grid-template-columns: repeat(4, minmax(0, 1fr));
  gap: 10px;
  margin: 8px 0 12px;
}
@media (max-width: 520px) {
  .score-cards { grid-template-columns: repeat(2, minmax(0, 1fr)); }
}

.stat-card {
  background: rgba(255,255,255,.04);
  border: 1px solid rgba(255,255,255,.08);
  border-radius: 12px;
  padding: 10px 12px;
}
.stat-label {
  font-size: 12px;
  color: var(--muted);
  margin-bottom: 4px;
}
.stat-value {
  font-size: 20px;
  font-weight: 800;
  letter-spacing: .3px;
}

/* Progress */
.progress-wrap {
  margin-top: 6px;
}
.progress-top {
  display: flex;
  justify-content: space-between;
  color: var(--muted);
  font-size: 12px;
  margin-bottom: 6px;
}
.progress-bar {
  height: 10px;
  background: rgba(255,255,255,.06);
  border: 1px solid rgba(255,255,255,.08);
  border-radius: 999px;
  overflow: hidden;
}
.progress-fill {
  height: 100%;
  background: linear-gradient(90deg, var(--accent), var(--accent-2));
  width: 0%;
  transition: width .35s ease;
}

